import React, { Component } from "react";
import AdminNavbar from "../../../Components/AdminNavbar/AdminNavbar";
import { Pagination, Container, Card, Table, Badge } from "react-bootstrap";
import { withRouter } from "react-router-dom";
import {
  mapStateToProps,
  mapDispatchToProps
} from "../../../Config/AdminMapping";
import { connect } from "react-redux";

class Reservations extends Component {
  redirectToCustomer = async uid => {
    this.props.history.push("/admin/customer/" + uid);
  };
  render() {
    const reservationsToRender = this.props.reservations.map(element => {
      const customer = this.props.customers.find(
        c => c.uid === element.customer_uid
      );
      const vendor = this.props.vendors.find(
        c => c.uid === element.restaurant_uid
      );
      const badgeVariant =
        element.status == "Reserved"
          ? "warning"
          : element.status == "Completed"
          ? "success"
          : "danger";
      return (
        <tr onClick={() => this.redirectToCustomer(customer.uid)}>
          <td>
            <img
              style={{
                width: 40,
                height: 40,
                borderRadius: 50,
                objectFit: "cover"
              }}
              src={customer.photo}
            ></img>
          </td>
          <td>{customer.name}</td>
          <td>{vendor ? vendor.name : "Vendor Deleted"}</td>
          <td>{element.time}</td>
          <td>{element.date}</td>
          <td>{element.attandee}</td>
          <td>
            <Badge variant={badgeVariant}>{element.status}</Badge>
          </td>
        </tr>
      );
    });
    return (
      <div>
        <AdminNavbar></AdminNavbar>
        <Container>
          <Card className="my-3">
            <Card.Header>
              <h1>Reservations</h1>
            </Card.Header>
            <Card.Body>
              <Table responsive striped hover>
                <thead>
                  <tr>
                    <th>Customer Image</th>
                    <th>Customer Name</th>
                    <th>Vendor Name</th>
                    <th>Reservation Time</th>
                    <th>Date of Reservation</th>
                    <th>Number of Attendees</th>
                    <th>Status</th>
                  </tr>
                </thead>
                <tbody>{reservationsToRender}</tbody>
              </Table>
            </Card.Body>
          </Card>
        </Container>
      </div>
    );
  }
}
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Reservations));
