import React, { Component } from "react";
import AdminNavbar from "../../../Components/AdminNavbar/AdminNavbar";
import {
  Tabs,
  Container,
  Col,
  Card,
  Pagination,
  Badge,
  Button,
  Modal,
  InputGroup,
  FormControl
} from "react-bootstrap";
import { Tab } from "react-bootstrap";
import { Row } from "react-bootstrap";
import { Table } from "react-bootstrap";
import "./Dashboard.css";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import {
  mapStateToProps,
  mapDispatchToProps
} from "../../../Config/AdminMapping";
import Firebase from "../../../Firebase/Firebase";
class Dashboard extends Component {
  state = {
    modalShow: false,
    selectedVendor: "",
    procFee: 0
  };
  clickHandler = async uid => {
    this.props.history.push("/admin/vendor/" + uid);
  };
  render() {
    const restaurants = this.props.vendors.filter(
      element => element.category === "Restaurants" && element.is_verified
    );
    const escapeRooms = this.props.vendors.filter(
      element => element.category === "Escape Rooms" && element.is_verified
    );
    const cafes = this.props.vendors.filter(
      element => element.category === "Cafes" && element.is_verified
    );
    const events = this.props.vendors.filter(
      element => element.category === "Events" && element.is_verified
    );
    const unverified = this.props.vendors.filter(
      element => !element.is_verified
    );

    return (
      <div>
        <AdminNavbar></AdminNavbar>
        <Container>
          <Row className="justify-content-center">
            <Col xs={12}>
              <Card>
                <Card.Header>
                  <h1>Vendors</h1>
                </Card.Header>
                <Card.Body>
                  <Tabs
                    variant="pills"
                    defaultActiveKey="Restaurants"
                    id="noanim-tab-example"
                  >
                    <Tab eventKey="Restaurants" title="Restaurants">
                      <Table striped hover responsive>
                        <thead>
                          <tr>
                            <th>Image</th>
                            <th>Name</th>
                            <th>Email</th>
                            <th>Status</th>
                          </tr>
                        </thead>
                        <tbody>
                          {restaurants.map(element => {
                            return (
                              <>
                                <tr
                                  onClick={() => this.clickHandler(element.uid)}
                                >
                                  <td>
                                    <img
                                      style={{
                                        width: 40,
                                        height: 40,
                                        borderRadius: 50,
                                        objectFit: "cover"
                                      }}
                                      src={element.photo_url}
                                    ></img>
                                  </td>
                                  <td>{element.name}</td>
                                  <td>{element.email}</td>
                                  <td>
                                    {element.is_verified ? (
                                      <Badge variant="success">Verified</Badge>
                                    ) : (
                                      <Badge variant="danger">Unverified</Badge>
                                    )}
                                  </td>
                                </tr>
                              </>
                            );
                          })}
                        </tbody>
                      </Table>
                      <Pagination>
                        <Pagination.First />
                        <Pagination.Prev />
                        <Pagination.Item>{1}</Pagination.Item>
                        <Pagination.Item>{2}</Pagination.Item>
                        <Pagination.Item>{3}</Pagination.Item>
                        <Pagination.Item>{4}</Pagination.Item>
                        <Pagination.Item>{5}</Pagination.Item>
                        <Pagination.Next />
                        <Pagination.Last />
                      </Pagination>
                    </Tab>
                    <Tab eventKey="Cafe" title="Cafe">
                      <Table striped hover responsive>
                        <thead>
                          <tr>
                            <th>Image</th>
                            <th>Name</th>
                            <th>Email</th>
                            <th>Status</th>
                          </tr>
                        </thead>
                        <tbody>
                          {cafes.map(element => {
                            return (
                              <>
                                <tr
                                  onClick={() => this.clickHandler(element.uid)}
                                >
                                  <td>
                                    <img
                                      style={{
                                        width: 40,
                                        height: 40,
                                        borderRadius: 50,
                                        objectFit: "cover"
                                      }}
                                      src={element.photo_url}
                                    ></img>
                                  </td>
                                  <td>{element.name}</td>
                                  <td>{element.email}</td>
                                  <td>
                                    {element.is_verified ? (
                                      <Badge variant="success">Verified</Badge>
                                    ) : (
                                      <Badge variant="danger">Unerified</Badge>
                                    )}
                                  </td>
                                </tr>
                              </>
                            );
                          })}
                        </tbody>
                      </Table>
                      <Pagination>
                        <Pagination.First />
                        <Pagination.Prev />
                        <Pagination.Item>{1}</Pagination.Item>
                        <Pagination.Item>{2}</Pagination.Item>
                        <Pagination.Item>{3}</Pagination.Item>
                        <Pagination.Item>{4}</Pagination.Item>
                        <Pagination.Item>{5}</Pagination.Item>
                        <Pagination.Next />
                        <Pagination.Last />
                      </Pagination>
                    </Tab>
                    <Tab eventKey="Escape Rooms" title="Escape Rooms">
                      <Table striped hover responsive>
                        <thead>
                          <tr>
                            <th>Image</th>
                            <th>Name</th>
                            <th>Email</th>
                            <th>Status</th>
                          </tr>
                        </thead>
                        <tbody>
                          {escapeRooms.map(element => {
                            return (
                              <>
                                <tr
                                  onClick={() => this.clickHandler(element.uid)}
                                >
                                  <td>
                                    <img
                                      style={{
                                        width: 40,
                                        height: 40,
                                        borderRadius: 50,
                                        objectFit: "cover"
                                      }}
                                      src={element.photo_url}
                                    ></img>
                                  </td>
                                  <td>{element.name}</td>
                                  <td>{element.email}</td>
                                  <td>
                                    {element.is_verified ? (
                                      <Badge variant="success">Verified</Badge>
                                    ) : (
                                      <Badge variant="danger">Unerified</Badge>
                                    )}
                                  </td>
                                </tr>
                              </>
                            );
                          })}
                        </tbody>
                      </Table>
                      <Pagination>
                        <Pagination.First />
                        <Pagination.Prev />
                        <Pagination.Item>{1}</Pagination.Item>
                        <Pagination.Item>{2}</Pagination.Item>
                        <Pagination.Item>{3}</Pagination.Item>
                        <Pagination.Item>{4}</Pagination.Item>
                        <Pagination.Item>{5}</Pagination.Item>
                        <Pagination.Next />
                        <Pagination.Last />
                      </Pagination>
                    </Tab>
                    <Tab eventKey="Event Organizers" title="Event Organizers">
                      <Table striped hover responsive>
                        <thead>
                          <tr>
                            <th>Image</th>
                            <th>Name</th>
                            <th>Email</th>
                            <th>Status</th>
                          </tr>
                        </thead>
                        <tbody>
                          {events.map(element => {
                            return (
                              <>
                                <tr
                                  onClick={() => this.clickHandler(element.uid)}
                                >
                                  <td>
                                    <img
                                      style={{
                                        width: 40,
                                        height: 40,
                                        borderRadius: 50,
                                        objectFit: "cover"
                                      }}
                                      src={element.photo_url}
                                    ></img>
                                  </td>
                                  <td>{element.name}</td>
                                  <td>{element.email}</td>
                                  <td>
                                    {element.is_verified ? (
                                      <Badge variant="success">Verified</Badge>
                                    ) : (
                                      <Badge variant="danger">Unerified</Badge>
                                    )}
                                  </td>
                                </tr>
                              </>
                            );
                          })}
                        </tbody>
                      </Table>
                      <Pagination>
                        <Pagination.First />
                        <Pagination.Prev />
                        <Pagination.Item>{1}</Pagination.Item>
                        <Pagination.Item>{2}</Pagination.Item>
                        <Pagination.Item>{3}</Pagination.Item>
                        <Pagination.Item>{4}</Pagination.Item>
                        <Pagination.Item>{5}</Pagination.Item>
                        <Pagination.Next />
                        <Pagination.Last />
                      </Pagination>
                    </Tab>
                    <Tab eventKey="Unverified" title="Unverified">
                      <Table striped hover responsive>
                        <thead>
                          <tr>
                            <th>Image</th>
                            <th>Name</th>
                            <th>Email</th>
                            <th>Reservation Price</th>
                            <th>Status</th>
                            <th></th>
                          </tr>
                        </thead>
                        <tbody>
                          {unverified.map(element => {
                            return (
                              <>
                                <tr>
                                  <td
                                    onClick={() =>
                                      this.clickHandler(element.uid)
                                    }
                                  >
                                    <img
                                      style={{
                                        width: 40,
                                        height: 40,
                                        borderRadius: 50,
                                        objectFit: "cover"
                                      }}
                                      src={
                                        element.photo_url
                                          ? element.photo_url
                                          : "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRwYsBVC-oKRvO87aXVfE63jYe52NcDYM5ArsCRKAGZaImUHAql"
                                      }
                                    ></img>
                                  </td>
                                  <td>{element.name}</td>
                                  <td>{element.email}</td>
                                  <td>{element.reservation_fee}</td>
                                  <td>
                                    {element.is_verified ? (
                                      <Badge variant="success">Verified</Badge>
                                    ) : (
                                      <Badge variant="danger">Unerified</Badge>
                                    )}
                                  </td>
                                  <td>
                                    <Button
                                      onClick={() => {
                                        this.setState({
                                          modalShow: true,
                                          selectedVendor: element.uid
                                        });
                                      }}
                                      variant="success"
                                      size="sm"
                                    >
                                      Verify
                                    </Button>
                                  </td>
                                </tr>
                              </>
                            );
                          })}
                        </tbody>
                      </Table>
                      <Pagination>
                        <Pagination.First />
                        <Pagination.Prev />
                        <Pagination.Item>{1}</Pagination.Item>
                        <Pagination.Item>{2}</Pagination.Item>
                        <Pagination.Item>{3}</Pagination.Item>
                        <Pagination.Item>{4}</Pagination.Item>
                        <Pagination.Item>{5}</Pagination.Item>
                        <Pagination.Next />
                        <Pagination.Last />
                      </Pagination>
                    </Tab>
                  </Tabs>
                </Card.Body>
              </Card>
            </Col>
          </Row>
        </Container>{" "}
        <Modal
          size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
          show={this.state.modalShow}
          onHide={() => this.setState({ modalShow: false })}
        >
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
              Edit Processing Fee
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <InputGroup className="mb-3">
              <InputGroup.Prepend>
                <InputGroup.Text id="inputGroup-sizing-default">
                  Processing Fee
                </InputGroup.Text>
              </InputGroup.Prepend>
              <FormControl
                aria-label="Default"
                aria-describedby="inputGroup-sizing-default"
                type="number"
                onChange={e => {
                  this.setState({
                    newProFee: e.target.value
                  });
                }}
              />
            </InputGroup>
          </Modal.Body>
          <Modal.Footer>
            <Button
              onClick={async () => {
                await Firebase.updateProcessingFeeAndVerify(
                  this.state.newProFee,
                  this.state.selectedVendor
                );
                this.setState({ modalShow: false });
              }}
            >
              Update
            </Button>
            <Button
              variant="danger"
              onClick={() => this.setState({ modalShow: false })}
            >
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Dashboard));
