import React, { Component } from "react";
import "./Login.css";
import Logo from "../../../Images/logo.png";
import { withRouter } from "react-router-dom";
import Firebase from "../../../Firebase/Firebase";
import { setAdmin } from "../../../Config/localStorage";
import { connect } from "react-redux";
import {
  mapStateToProps,
  mapDispatchToProps
} from "../../../Config/AdminMapping";
class Login extends Component {
  state = {
    email: "",
    password: ""
  };
  changeState = (name, value) => {
    this.setState({
      [name]: value
    });
  };
  login = async () => {
    if (this.state.email.trim() !== "" && this.state.password.trim() !== "") {
      const user = await Firebase.login(this.state.email, this.state.password);
      setAdmin(user);
      this.props.updateLogin(user);
      window.location.reload();
    } else {
      alert("Your have to fill all fields in order to login");
    }
  };

  render() {
    return (
      <div>
        {/* <HomeNavbar></HomeNavbar> */}
        <div className="container ">
          <div className="row justify-content-center">
            <img
              style={{
                width: "20%",
                marginTop: "10vh"
              }}
              src={Logo}
            ></img>
          </div>
          <div className="row justify-content-center">
            <form className="loginAdmin col-10 col-md-6">
              <legend>Admin Login</legend>
              <div className="form-group">
                <label htmlFor="exampleInputEmail1">
                  Enter Your Email Address
                </label>
                <input
                  onChange={e => this.changeState("email", e.target.value)}
                  type="email"
                  className="form-control"
                  id="exampleInputEmail1"
                  placeholder="Email Address"
                  aria-describedby="emailHelp"
                />
              </div>
              <div className="form-group">
                <label htmlFor="password">Enter Your Password</label>
                <input
                  onChange={e => this.changeState("password", e.target.value)}
                  type="password"
                  className="form-control"
                  id="password"
                  placeholder="Password"
                />
              </div>
              <button
                onClick={this.login}
                type="button"
                className="btn btn-primary btn-block"
              >
                Login
              </button>
            </form>
          </div>
        </div>
        {/* <Footer></Footer> */}
      </div>
    );
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Login));
