import React, { Component } from "react";
import AdminNavbar from "../../../Components/AdminNavbar/AdminNavbar";
import {
  Container,
  Card,
  Pagination,
  Table,
  Badge,
  Form,
  Button,
  Col,
  Modal
} from "react-bootstrap";
import { connect } from "react-redux";
import {
  mapStateToProps,
  mapDispatchToProps
} from "../../../Config/AdminMapping";
import "./DiscountVouchers.css";
import Firebase from "../../../Firebase/Firebase";
class DiscountVouchers extends Component {
  state = {
    code: "",
    expDate: "",
    maxUses: 0,
    vendor_type: "Restaurants",
    show: false,
    selected_uid: ""
  };
  showModal = () => {
    this.setState({ show: !this.state.show });
  };
  add = async () => {
    await Firebase.addDiscountVoucher(
      this.state.code,
      this.state.expDate,
      this.state.maxUses,
      this.state.vendor_type
    );
    window.location.reload();
  };
  handler = (name, value) => {
    console.log(value);
    this.setState({
      [name]: value
    });
  };
  dateHandler = e => {
    const splitDate = e.target.value.split("-");

    this.setState({
      expDate: splitDate[2] + "/" + splitDate[1] + "/" + splitDate[0]
    });
  };
  block = async (uid, value) => {
    await Firebase.updateDiscountVoucher(uid, !value);
    window.location.reload();
  };
  render() {
    const vendors = this.props.vendors.map(e => {
      return <option value={e.uid}>{e.name}</option>;
    });
    return (
      <div>
        <AdminNavbar></AdminNavbar>
        <Container>
          <Card className="my-3">
            <Card.Header>
              <h1>Discount Vouchers</h1>
            </Card.Header>
            <Card.Body>
              <Form>
                <legend className="h4">Add Discount Voucher</legend>
                <Form.Row>
                  <Form.Group as={Col} controlId="formGridEmail">
                    <Form.Label>Code</Form.Label>
                    <Form.Control
                      onChange={e => this.handler("code", e.target.value)}
                      type="text"
                      placeholder="Enter Code"
                    />
                  </Form.Group>

                  <Form.Group as={Col} controlId="expiryDate">
                    <Form.Label>Expiry Date</Form.Label>
                    <Form.Control
                      onChange={this.dateHandler}
                      type="date"
                      placeholder="Expiry Date"
                    />
                  </Form.Group>
                </Form.Row>
                <Form.Group controlId="exampleForm.ControlSelect1">
                  <Form.Label>Vendor Type</Form.Label>
                  <Form.Control
                    onChange={e => this.handler("vendor_type", e.target.value)}
                    as="select"
                  >
                    <option value={"Restaurants"}>{"Restaurants"}</option>
                    <option value={"Cafes"}>{"Cafes"}</option>
                    <option value={"Escape Rooms"}>{"Escape Rooms"}</option>
                    <option value={"Events"}>{"Events"}</option>
                  </Form.Control>
                </Form.Group>
                <Form.Group controlId="formGridPassword">
                  <Form.Label>Max Number of Uses</Form.Label>
                  <Form.Control
                    type="number"
                    placeholder="Max Uses"
                    value={this.state.maxUses}
                    onChange={e => {
                      if (e.target.value >= 0) {
                        this.handler("maxUses", e.target.value);
                      }
                    }}
                  />
                </Form.Group>
                <Button
                  onClick={this.add}
                  block
                  variant="primary"
                  type="button"
                >
                  Add New Voucher
                </Button>
              </Form>
              <hr></hr>
              <Table striped hover responsive>
                <thead>
                  <tr>
                    <th>Code</th>
                    <th>Times Used</th>
                    <th>Max Uses</th>
                    <th>Expiry Date</th>
                    <th>Vendor Type</th>
                    <th>Status</th>
                    <th>Change Status</th>
                    <th>Delete</th>
                  </tr>
                </thead>
                <tbody>
                  {this.props.vouchers.map(element => {
                    const date = new Date();
                    const splitExpDate = element.expDate.split("/");
                    const expDate = new Date(
                      Number(splitExpDate[2]),
                      Number(splitExpDate[1]) - 1,
                      Number(splitExpDate[0])
                    );
                    const condition =
                      element.isBlocked ||
                      Number(element.maxUses) <= Number(element.numberOfUses) ||
                      date > expDate;
                    return (
                      <tr>
                        <td>{element.code}</td>
                        <td>{element.numberOfUses}</td>

                        <td>{element.maxUses}</td>
                        <td>{element.expDate}</td>
                        <td>{element.vendor_type}</td>
                        <td>
                          <Badge variant={condition ? "danger" : "success"}>
                            {condition ? "Unavailable" : "Available"}
                          </Badge>
                        </td>
                        <td
                          onClick={() => {
                            this.block(element.uid, element.isBlocked);
                          }}
                        >
                          <ion-icon
                            class={
                              condition
                                ? "blockButton text-success"
                                : "blockButton text-danger"
                            }
                            name={
                              condition
                                ? "checkmark-circle-outline"
                                : "close-circle-outline"
                            }
                          ></ion-icon>
                        </td>
                        <td
                          onClick={async () => {
                            this.setState({
                              selected_uid: element.uid
                            });
                            this.showModal();
                          }}
                        >
                          <ion-icon
                            class="blockButton text-danger"
                            name="trash-outline"
                          ></ion-icon>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </Table>
              {/* <Pagination>
                <Pagination.First />
                <Pagination.Prev />
                <Pagination.Item>{1}</Pagination.Item>
                <Pagination.Item>{2}</Pagination.Item>
                <Pagination.Item>{3}</Pagination.Item>
                <Pagination.Item>{4}</Pagination.Item>
                <Pagination.Item>{5}</Pagination.Item>
                <Pagination.Next />
                <Pagination.Last />
              </Pagination> */}
            </Card.Body>
          </Card>
        </Container>
        <Modal
          centered
          show={this.state.show}
          onHide={this.showModal}
          animation={true}
        >
          <Modal.Header closeButton>
            <Modal.Title>
              Are you sure you want to delete this voucher?
            </Modal.Title>
          </Modal.Header>
          <Modal.Footer>
            <Button
              variant="primary"
              onClick={async () => {
                await Firebase.deleteDiscount(this.state.selected_uid);
                window.location.reload();
              }}
            >
              Yes
            </Button>
            <Button variant="danger" onClick={this.showModal}>
              No
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(DiscountVouchers);
